import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AuthGuardService } from '../auth0/auth-guard.service';
import { ProjectsComponent } from '../projects/projects.component';

const ProjectsRouts: Routes = [
  { path: '', component: ProjectsComponent },
  {
    path: 'create',
    loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./project-form/project-form.module')['ProjectFormModule']);  });}),
    canActivate: [AuthGuardService],
  },
  {
    path: ':path/edit',
    loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./project-form/project-form.module')['ProjectFormModule']);  });}),
    canActivate: [AuthGuardService],
  },
  {
    path: ':ProjectName',
    loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./project-details/project-details.module')['ProjectDetailsModule']);  });}),
  },
  {
    path: ':showcaseName/:ProjectName/:sortBy/:sortOrder',
    loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./project-details/project-details.module')['ProjectDetailsModule']);  });}),
  },
];

@NgModule({
  imports: [RouterModule.forChild(ProjectsRouts)],
  exports: [RouterModule],
  declarations: [],
})
export class ProjectsRoutingModule {}



